.field {
  background: #ffffff;
  border: 1px solid #efefef;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 8px 12px;
  width: 100%;
  // font-size: 14px;

  &:focus {
    border-color: #dbdbdb;
  }
}